import cuModal from "@/meetu-ui/components/cu-modal.vue";
import { mapGetters } from 'vuex';
export default {
	name: 'homeIndex',
	components: {
		cuModal
	},
	data() {
		return {
			modalName: '', // 弹窗类型-modal/bottomModal
			appName: 'Meet U',
			showSendToast: false,
			userNumber: null,
			notreadNum: 0
		}
	},
	computed: {
		...mapGetters(['userInfo'])
	},
	onLoad() {
		this.api_UserInfo();
		this.api_UserNumber();
	},
	onReady() {
		if(uni.getStorageSync('homeSendToast')) {
			this.showSendToast = false;
		} else {
			this.showSendToast = true;
		}
	},
	methods: {
		api_UserInfo(){
			this.$http1.post('user/info').then(res=>{
				// this.userinfo = res.data;
				this.$store.dispatch('changeVal', {stateKey: 'userInfo', newValue: res.data})
			}).catch(err=>{
				console.log('userinfo-err', err);
			})
		},
		api_UserNumber() {//操作剩余次数
			this.$http1.post('user/number').then(res=>{
				console.log('userNumber', res.data);
				this.userNumber = res.data;
			}).catch(err=>{
				
			})
		},
		linkUser() {
			uni.navigateTo({
				url: '../user/index',
				animationDuration: 300,
				animationType: 'slide-in-bottom'
			})
		},
		linkAction(type) {
			switch (type){
				case 'search':
					uni.navigateTo({
						url: '../search/search',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'send':
					if(!uni.getStorageSync('homeSendToast')) {
						uni.setStorageSync('homeSendToast', true);
						this.showSendToast = false;
					} 
					this.modalName = 'bottomModal';
					break;
				case 'chatList':
					uni.navigateTo({
						url: '../chat/list',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendText':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/text',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendVoice':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/voice',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
			}
		},
		hideModal() {
			this.modalName = '';
		}
	}
}
